import React,{Fragment, useState, useEffect} from 'react';
import AppBar from '@material-ui/core/AppBar';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';


export default function Contenedor({pokemon, classes}){
  const [pokemonData, setPokemonData] = useState({});

	const getPokemonCard = (card, image) => {
		return (
			<Grid item key={card.name} xs={12} sm={6} md={4}>
				<Card className={classes.card}>
					<CardMedia
						className={classes.cardMedia}
						image={image}
						title="Image title"
						/>
						<CardContent className={classes.cardContent}>
						<Typography gutterBottom variant="h5" component="h2">
							{card.name}
						</Typography>
						<Typography>
						Descripcion
						</Typography>
					</CardContent>
				
				</Card>
			</Grid>
		);
  	};
	return (
		<Fragment>
			<Container className={classes.cardGrid} maxWidth="md">
					{/* End hero unit */}
					<Grid container spacing={4}>
				 

						{pokemon.map((card, index) => {
							let url = "https://github.com/PokeAPI/sprites/blob/master/sprites/pokemon/other-sprites/official-artwork/"
          					let pokeIndex = card.url.split('/')[card.url.split('/').length - 2]
          					let image = `${url}${pokeIndex}.png?raw=true` 

              				return getPokemonCard(card, image)
						 })}
					</Grid>
				</Container>
		</Fragment>
	)
}